:root {
  --canopy-green: #014038;
  --meadow-mist: #96BAA0;
  --sky-bloom: #BDD4DA;
  --sunlit-petal: #F0C06D;
  --cloudy-grey: #F7F7F7;
  --willow-gray: #C5C3C3;
}

/* Example:
h1 {
  font-size: rem(30);  <-- this is 1.875rem
}
*/
:root {
  /* Title Large */
  --font-titleLarge: "Arial", sans-serif;
  --size-titleLarge: 30px;
  --weight-titleLarge: bold;
  /* Title */
  --font-title: "Arial", sans-serif;
  --size-title: 25px;
  --weight-title: normal;
  /* Subtitle */
  --font-subtitle: "Comfortaa", sans-serif;
  --size-subtitle: 15px;
  --weight-subtitle: normal;
  /* Body Large */
  --font-bodyLarge: "Calibri", sans-serif;
  --size-bodyLarge: 20px;
  --weight-bodyLarge: normal;
  /* Body Strong */
  --font-bodyStrong: "Calibri", sans-serif;
  --size-bodyStrong: 16px;
  --weight-bodyStrong: bold;
  /* Body */
  --font-body: "Calibri", sans-serif;
  --size-body: 16px;
  --weight-body: normal;
  /* Captions */
  --font-captions: "Arial", sans-serif;
  --size-captions: 11px;
  --weight-captions: normal;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background: var(--canopy-green);
  color: var(--willow-gray);
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 69.375em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.titleLarge {
  font: var(--weight-titleLarge) var(--size-titleLarge) var(--font-titleLarge);
}

.title {
  font: var(--weight-title) var(--size-title) var(--font-title);
}

.subtitle {
  font: var(--weight-subtitle) var(--size-subtitle) var(--font-subtitle);
}

.bodyLarge {
  font: var(--weight-bodyLarge) var(--size-bodyLarge) var(--font-bodyLarge);
}

.bodyStrong {
  font: var(--weight-bodyStrong) var(--size-bodyStrong) var(--font-bodyStrong);
}

.body {
  font: var(--weight-body) var(--size-body) var(--font-body);
}

.captions {
  font: var(--weight-captions) var(--size-captions) var(--font-captions);
}

.container {
  padding: 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 69.375em) {
  .container {
    padding: 0;
  }
}

.header {
  background-color: #BDD4DA;
  color: #014038;
  padding: 15px 0;
  margin-top: 1.5rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 54.375em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (max-width: 54.3125em) {
  .header {
    padding: 0.625rem 0;
  }
}
.header__hamburger {
  display: flex;
  align-items: center;
  margin-left: 0.625rem;
}
.header__logo {
  display: flex;
  align-items: center;
  margin: 0 auto;
}
.header__search, .header__searchbar, .header__shoppingcart, .header__account {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: auto;
  margin-right: 0.625rem;
}
.header__search {
  margin-right: 0.125rem;
}
.header input[type=text] {
  padding-left: 0.625rem;
  box-sizing: border-box;
  border: 0 solid var(--sky-bloom);
  height: 1.875rem;
  border-radius: 0.9375rem;
  color: var(--canopy-green);
  background-color: var(--meadow-mist);
}
@media (max-width: 54.3125em) {
  .header input[type=text],
  .header .header__account {
    display: none;
  }
}
.header__left {
  display: flex;
  justify-content: flex-start;
  flex: 1;
}
.header__center {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 0.625rem;
  margin: 0 auto;
  text-align: center;
  position: center;
}
.header__right {
  display: flex;
  justify-content: flex-end;
  flex: 1;
}

.card {
  padding: 1.25rem;
}